import React, { useState } from "react";
import s from './ProfileInfo.module.css'
import Preloader from "../../../common/Preloader/Preloader";
import ProfileStatus from "./ProfileStatus.jsx";
import userPhoto from "./../../../../images/NFT-Avatar.png"
import ProfileDataForm from "./ProfileDataForm";

const ProfileInfo = (props) => {

    const [editMode, setEditMode] = useState(false);

    if (!props.profile) {
        return <Preloader />
    }
    const onMainPhotoSelected = (e) => {
        if (e.target.files.length) {
            props.savePhoto(e.target.files[0]);
        }
    }

    const onSubmit = (formData) => {
        props.saveProfile(formData)
        setEditMode(false);
    }

    return (
        <div>
            <div>
                <img className={s.pho} src='https://global-uploads.webflow.com/5ef5480befd392489dacf544/5f9f5e5943de7e69a1339242_5f44a7398c0cdf460857e744_img-image.jpeg' />
                <div className={s.userAva}>
                    <img src={props.profile.photos.large != null ? props.profile.photos.large : userPhoto} />
                </div>
            </div>
            {props.isOwner && <input type={"file"} onChange={onMainPhotoSelected} />}

            {editMode ? <ProfileDataForm initialValues={props.profile.fullName} profile={props.profile} onSubmit={onSubmit} /> : <ProfileData profile={props.profile} isOwner={props.isOwner} goToEditMode={ () => {setEditMode(true)}}/>}

            <div className={s.item}>
                <ProfileStatus status={props.status} updateStatus={props.updateStatus} />
            </div>
        </div>
    );
}

const ProfileData = (props) => {
    return <div className={s.status}>
        {props.isOwner && <div><button onClick={props.goToEditMode}>edit</button></div>}
        <div>
            <b>Full Name:</b> {props.profile.fullName}
        </div>
        <div>
            <b>Locking for a job:</b> {props.profile.lookingForAJob ? "yes" : "no"}
        </div>
        {props.profile.lookingForAJob &&
            <div>
                <b>My professional skills:</b> {props.profile.lookingForAJobDescription}
            </div>
        }
        <div>
            <b>About me:</b> {props.profile.aboutMe}
        </div>
        <div>
            <b>Contacts:</b> {Object.keys(props.profile.contacts).map(key => {
                return <Contact key={key} contactTitle={key} contactValue={props.profile.contacts[key]} />
            })}
        </div>
    </div>
}

const Contact = ({ contactTitle, contactValue }) => {
    return <div className={s.contact}><b>{contactTitle}</b>: {contactValue}</div>
}

export default ProfileInfo;

