import React from "react";
import s from "./ProfileInfo.module.css";
import { Field, reduxForm } from "redux-form";
import { Input, Textarea } from "../../../common/FormControls/FormControls.js";


const ProfileDataForm = (props) => {
    return <form onSubmit={props.handleSubmit}> 
    <div className={s.status}>
        <div><button onClick={() => {}}>save</button></div>
        <div>
            <b>Full Name:</b> <Field placeholder={"fullName"} name={"fullName"} component={Input}/>
        </div>
        <div>
            <b>Locking for a job:</b> <Field name={"lookingForAJob"} component={Input} type={"checkbox"} />
        </div>
            <div>
                <b>My professional skills:</b> <Field placeholder="My professional skills" name={"lookingForAJobDescription"} component={Textarea} />
            </div>
        <div>
            <b>About me:</b> <Field placeholder="About me" name={"aboutMe"} component={Textarea} />
        </div>
        <div>
            <b>Contacts:</b> {Object.keys(props.profile.contacts).map(key => {
                return <div key={key} className={s.contact}>
                    <b>{key} : <Field placeholder="key" name={"contacts." + key}  component={Input} /></b>
                </div>
            })}
        </div>
    </div>
    </form>
}

const ProfileDataFormRedux = reduxForm({
    form: "edit-profile",
})(ProfileDataForm)

export default ProfileDataFormRedux;